<template>
  <div class="deleteAddress" v-show="shifou">
    <div class="deleteAddressBox">
      <p><img src="../../assets/images/usercenter/Login_07.png"/>{{mes}}</p>
      <ul>
        <li class="areyousure" @click="deleteCancel"><a>{{text_l}}</a></li>
        <li class="deletele" @click="deleteSure"><a>{{text_r}}</a></li>
      </ul>
    </div>
  </div>
</template>
<script>

  export default {
    name:'modal',
    data() {
      return {
        shifou:false
      }
    },
    methods:{
      deleteCancel: function(){
        this.$emit('childSay');
      },
      deleteSure:function () {
        this.$emit('deletesure');
      }
    },
    props:["text_l","text_r","mes"],

  }
</script>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };
  .deleteAddress{
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    transition: opacity .3s ease;
    .deleteAddressBox{
      position: absolute;
      top:20%;
      left: 15%;
      width: 70%;
      -webkit-border-radius: 10px;
      -moz-border-radius: 10px;
      border-radius: 10px;
      height: n(300);
      background: #ffffff;
      overflow: hidden;
      p{
        width: 100%;
        height: n(220);
        display: inline-flex;
        align-items: center;
        justify-content: center;
        img{
          width: n(50);
          height: n(50);
        }
      }
      ul{
        width: 100%;
        height: n(80);
        .areyousure{
          float: left;
          width: 50%;
          height: n(80);
          text-align: center;
          line-height: n(80);
          background: #007add;
        }
        .deletele{
          float: left;
          width: 50%;
          height: n(80);
          text-align: center;
          line-height: n(80);
          background: #fff001;
        }
      }
    }
  }
</style>




