<template>
    <div>
        <div class="header">
          <img @click="back" src="../../assets/images/Login/game_1.png"/>
          <a>{{title}}</a>
        </div>
    </div>
</template>
<style lang="scss" scoped>

  @function n($n) {
    @return $n  / 64 + rem;
  };
  .header{
    position: fixed;
    top:0;
    left:0;
    width: 100%;
    height: n(90);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid #d8d8d8;
    img{
      position: absolute;
      left: 0.2rem;
      width: n(70);
      height: n(50);
      line-height: n(90);
    }
    a{
      width: 100%;
      display: inline-block;
      text-align: center;
      font-size: n(34);
    }
  }
</style>
<script>

    export default {
        data() {
            return {
            }
        },
      props:["title"],
      methods:{
        back:function () {
          this.$router.go(-1);
        },
      }
    }
</script>



